<template>
  <div class="closures">
    <v-container>
        <v-row justify-center dense>
          <v-col cols="12" lg="12" sm="12">
            <div class="d-flex justify-center flex-wrap"   >
                <Closure :view="id" :cursor="'pointer'" class="ma-1" v-for="(id, key) in $displayOrder($view.closure)" :key="key"/>
            </div>
          </v-col>
        </v-row>
    </v-container>
    <ReadingsDetails v-if="dialog" v-model="dialog" :id="activeID"/>
  </div>
</template>

<script>
import Closure from '@/components/Closure.vue'
import ReadingsDetails from '@/components/ReadingsDetailDialog.vue'


import eventBus from "../eventBus"

export default {
    name: 'ClosureVIew',
    components: {
        Closure,
        ReadingsDetails
    },
    data: () => ({
        dialog:false,
        activeID:'',
    }),
    created(){
        eventBus.$on('details',this.openDetailsDialog)
    },
    mounted (){
        window.scrollTo(0, 0)
    },
    methods:{
        openDetailsDialog(id){
          this.activeID = id
          this.dialog = !this.dialog 
        },
    }
}
</script>

<style scoped>
 .container {
    width: 100%;
    padding: 0px; 
  } 
  .closures {
    overflow: hidden;
  }
</style>